import React from 'react';

class Navbar extends React.Component {

    render(){
        return(
           <div class="grid grid-cols-4 text-center">
               <button onClick={() => this.props.onClick("Home")} className='border-r border-black py-2 hover:bg-yellow-100'>Home</button>
               <button onClick={() => this.props.onClick("About")} className='border-r border-black py-2 hover:bg-yellow-100'>About</button>
               <button onClick={() => this.props.onClick("Portfolio")} className='border-r border-black py-2 hover:bg-yellow-100'>Portfolio</button>
               <button onClick={() => this.props.onClick("Contact")} className='border-r border-black py-2 hover:bg-yellow-100'>Contact</button>
           </div>
        );
    }
}

export default Navbar;