import React from 'react';
import Navbar from './Navbar';
import Banner from './Banner';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      toggle : "Home",
    };
  }

  handleClick(i){
    this.setState({
      toggle : i,
    });
    console.log(this.state.toggle);
  }

  render() {
    return (
      <div class="grid grid-cols-2">
        <div class="col-span-2 border-2 border-black grid grid-cols-2 bg-red-300">
          <Navbar onClick={(i) => this.handleClick(i)}/>
          <div></div>
        </div>
        
        <div class="col-span-2 bg-yellow-100 pt-10 h-screen">
          <Banner version={this.state.toggle}/>
        </div>
      </div>
    );
  }
 

  
}

export default App;